import React from 'react';
import {Box, Text} from '@chakra-ui/react';

interface PropsTypes {
    index: number;
    onClick: () => void;
    isActive: boolean;
}

const ThemeItem: React.FC<PropsTypes> = ({index, onClick, isActive}) => {
    const title = index === 0 ? 'Пример с решением' : `Пример №${index}`;
    return <Box
        py='2'
        px='4'
        _hover={{bg: '#ececec'}}
        cursor='pointer'
        onClick={onClick}
        bg={isActive ? '#ececec' : 'transparent'}
    >
        <Text fontSize='md' fontWeight='medium'>{title}</Text>
    </Box>;
};

export default ThemeItem;