import React from 'react';
import Latex from 'react-latex-next';
import {Box, Text} from '@chakra-ui/react';

import {Theme} from '../api/types';
import ParsedText from './ParsedText';
import Hints from './Hints';

interface PropsTypes {
    task: {id: string, index: number};
    themes: Theme[];
}

const Displayer: React.FC<PropsTypes> = ({task, themes}) => {
    const theme = themes.find((item) => item.id === task.id) as Theme;

    if (task.index === 0) {
        return <Box
            flex={1}
            maxH='80vh'
        >
            <Text fontSize='xl' fontWeight='bold'>Задание</Text>
            <ParsedText value={theme.example.task} />
            <Text fontSize='xl' fontWeight='bold' mt='4'>Решение</Text>
            <Box>
                {theme.example.solution.map((item) => {
                    if (item.type === 'math') {
                        return <Box display='flex' justifyContent='center' my='4'>
                            <Latex>{item.content}</Latex>
                        </Box>;
                    }
                    return <ParsedText value={item.content} />;
                })}
            </Box>
            <Box display='flex' alignItems='center' mt='4'>
                <Text fontWeight='bold' mr='1' fontSize='xl'>Ответ:</Text>
                <Latex>{theme.example.answer}</Latex>
            </Box>
        </Box>;
    }

    return <Box
        flex={1}
        overflowY='auto'
        maxH='80vh'
    >
        <Text fontSize='xl' fontWeight='bold'>Задание</Text>
        <ParsedText value={theme.tasks[task.index - 1].task} />
        <Hints data={theme.tasks[task.index - 1].hints} />
    </Box>;
};

export default Displayer;
