import {useEffect, useState} from 'react';

interface IRequest {
    route: string;
    body?: object;
    method?: 'POST' | 'GET';
}

export type IResponse = {
    status: number;
    data: any;
} | null;

// const URL = 'https://dashka.cyclic.app/api';
const URL = 'https://91.203.66.65:80/api';

const makeRequest = async ({
    route,
    body,
    method = 'GET'
}: IRequest): Promise<IResponse> => {
    try {
        const rawRes = await fetch(`${URL}${route}`, {
            body: JSON.stringify(body),
            method,
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }
        });
        const res = await rawRes.json();
        return {
            status: rawRes.status,
            data: res
        };
    } catch (e) {
        return null;
    }
};

const useFetch = (props: IRequest): [IResponse, boolean] => {
    const [res, setRes] = useState<IResponse>(null);
    const [isLoading, setIsLoading] = useState(true);
    useEffect(() => {
        makeRequest(props).then((data) => {
            setRes(data);
            setIsLoading(false);
        });
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);
    return [res, isLoading];
};

export default useFetch;