import React, {useState} from 'react';
import {useParams} from 'react-router-dom';
import {Flex, Container, Heading, Spinner, Accordion, AccordionItem, AccordionButton, AccordionIcon, AccordionPanel, Text, Box} from '@chakra-ui/react';

import useMethodQuery from '../api/methods/useMethodQuery';
import MainLayout from '../layout/MainLayout';
import ThemeItem from '../components/ThemeItem';
import Displayer from '../components/Displayer';

const MethodPage: React.FC = () => {
    const {id} = useParams();
    const [res] = useMethodQuery(id);
    const [activeTask, setActiveTask] = useState<{id: string, index: number} | null>(null);

    return <MainLayout>
        <Container maxW='full' pt='8' px='32' display='flex'>
            <Box maxW={'40%'} mr='16'>
                {res && <Heading as='h2'>{res.title}</Heading>}
                {!res ? <Flex justifyContent='center' mt='48'>
                    <Spinner size='lg' />
                </Flex> : <Accordion mt={12} allowToggle>
                    {res.themes.map((item) => <AccordionItem key={item.id}>
                        <AccordionButton _expanded={{bg: '#2b6cb0', color: 'white'}} py='3'>
                            <Text fontSize='xl' fontWeight='medium' flex={1} textAlign='left'>
                                {item.title}
                            </Text>
                            <AccordionIcon />
                        </AccordionButton>
                        <AccordionPanel p={0}>
                            <ThemeItem
                                index={0}
                                isActive={activeTask?.index === 0}
                                onClick={() => setActiveTask({id: item.id, index: 0})}
                            />
                            {item.tasks.map((el, index) => <ThemeItem
                                index={index + 1}
                                isActive={activeTask?.index === index + 1}
                                onClick={() => setActiveTask({id: item.id, index: index + 1})}
                            />)}
                        </AccordionPanel>
                    </AccordionItem>)}
                </Accordion>}
            </Box>
            {res && activeTask && <Displayer
                task={activeTask}
                themes={res.themes}
            />}
        </Container>
    </MainLayout>;
};

export default MethodPage;