import {MethodItemType} from '../types';
import useFetch from '../useFetch';

const useMethodsQuery = (): [null | MethodItemType[], boolean] => {
    const [res, isLoading] = useFetch({
        route: '/methods'
    });
    const methods = res !== null ? res.data as MethodItemType[] : null;
    return [methods, isLoading];
};

export default useMethodsQuery;