import {MethodType} from '../types';
import useFetch from '../useFetch';

const useMethodQuery = (id?: string): [null | MethodType, boolean] => {
    const [res, isLoading] = useFetch({
        route: `/methods/${id}`
    });
    if (!id) return [null, true];
    const method = res !== null ? res.data as MethodType : null;
    return [method, isLoading];
};

export default useMethodQuery;