import React from 'react';
import {Text} from '@chakra-ui/react';
import Latex from 'react-latex-next';

interface PropsTypes {
    value: string;
    mt?: number | string;
}

type ParsedItem = {
    id: string;
    type: string;
    value: string;
}

const OPTIONS = ['MATH'];

function findIndex(str: string, from = 0): number {
    let counter = 0;
    while (from < str.length) {
        if (str[from] === '{') counter++;
        if (str[from] === '}') {
            if (counter === 0) {
                return from;
            }
            counter--;
        }
        from++;
    }
    return -1;
};

function parse(str: string): ParsedItem[] {
    const result: ParsedItem[] = [];
    let currentPos = 0;
    for (let i = 0; i < str.length; i++) {
        for (let j = 0; j < OPTIONS.length; j++) {
            const opt = OPTIONS[j];
            if (str.slice(i, i + opt.length) !== opt) continue;
            const prevText = str.slice(currentPos, i);
            if (prevText.length !== 0) {
                result.push({
                    id: result.length.toString(),
                    type: 'text',
                    value: prevText,
                });
            }
            const index = findIndex(str, i + opt.length + 1);
            result.push({
                id: result.length.toString(),
                type: opt.toLowerCase(),
                value: str.slice(i + opt.length + 1, index),
            });
            i = index + 1;
            currentPos = i;
            break;
        }
    }
    const last = str.slice(currentPos);
    if (last.length !== 0) {
        result.push({
            id: result.length.toString(),
            type: 'text',
            value: last,
        });
    }
    return result;
};

const Item: React.FC<Omit<ParsedItem, 'id'>> = ({
    type,
    value,
}) => {
    switch (type) {
        case 'math':
            return <Latex>{value}</Latex>;
    }
    return <Text as='span'>{value}</Text>;
};

const MemoItem = React.memo(Item);

const ParsedText: React.FC<PropsTypes> = ({value, mt = '4'}) => {
    const result = parse(value);
    return <Text textIndent='32px' mt={mt} lineHeight='32px' fontSize='ls'>
        {result.map((item) => <MemoItem
            key={item.id}
            type={item.type}
            value={item.value}
        />)}
    </Text>;
};

export default ParsedText;
