import React from 'react';
import {Box, Button, Popover, PopoverBody, PopoverCloseButton, PopoverContent, PopoverTrigger, Text} from '@chakra-ui/react';
import ParsedText from './ParsedText';

interface PropsTypes {
    data: string[]
}

const Hints: React.FC<PropsTypes> = ({data}) => {
    if (data.length === 0) return null;
    return <Box mt='4'>
        <Text fontSize='xl' fontWeight='bold'>Подсказки</Text>
        <Box display='flex' gap={16} mt='2'>
            {data.map((item, index) => (
                <Popover key={index.toString()}>
                    <PopoverTrigger>
                        <Button>Подсказка {index + 1}</Button>
                    </PopoverTrigger>
                    <PopoverContent>
                        <PopoverCloseButton />
                        <PopoverBody>
                            <ParsedText value={item} mt={0} />
                        </PopoverBody>
                    </PopoverContent>
                </Popover>
            ))}
        </Box>
    </Box>;
};

export default Hints;
