import React from 'react';
import {Link} from 'react-router-dom';
import {Flex, Button, Card, CardFooter, CardHeader, Container, Heading, SimpleGrid, Spinner} from '@chakra-ui/react';

import useMethodsQuery from '../api/methods/useMethodsQuery';
import MainLayout from '../layout/MainLayout';

const HomePage: React.FC = () => {
    const [res] = useMethodsQuery();

    return <MainLayout>
        <Container maxW='5xl' pt='8'>
            <Heading as='h1'>Эвристические приёмы для решения математических задач</Heading>
            {!res ? <Flex justifyContent='center' mt='48'>
                <Spinner size='lg' />
            </Flex> : <SimpleGrid
                mt='8'
                spacing={8}
                templateColumns='repeat(auto-fill, minmax(300px, 1fr))'
            >
                {res.map((item) => <Card
                    key={item.id}
                    size='sm'
                    paddingX='2'
                    paddingY='1'
                    shadow={0}
                    borderWidth={4}
                    borderColor="blue.600"
                    borderRadius={12}
                >
                    <CardHeader h="100%">
                        <Heading size='md' fontWeight='medium'>{item.title}</Heading>
                    </CardHeader>
                    <CardFooter justifyContent='end'>
                        <Link to={`/methods/${item.id}`}>
                            <Button color="white" bg="blue.600" _hover={{bg: "blue.800"}}>Открыть</Button>
                        </Link>
                    </CardFooter>
                </Card>)}
            </SimpleGrid>}
        </Container>
    </MainLayout>;
};

export default HomePage;